"use client"

import Link from 'next/link'
import { usePathname } from 'next/navigation'
import { Tag } from "@phosphor-icons/react";



const Navigation = ({ links, mobile }: { links: any[], mobile: boolean }) => {
    const page = usePathname();

    return (
        <>
            {mobile ? (
                <nav className="flex bg-white h-[70px]  border-t-[1px] w-full fixed bottom-0 z-10 items-center justify-around px-3">
                    {links.map((link, index) => (
                        <Link
                            key={index}
                            className={`flex flex-col items-center px-2 ${page === link.href ? "text-black" : "text-gray-500"
                                }`}
                            href={link.href}
                        >
                            {link.icon}
                            <small className="text-xs">{link.title}</small>
                        </Link>
                    ))}
                </nav>
            ) : (
                <nav className="flex w-full gap-4 items-center justify-center">
                    {links.map((link, index) => (
                        <Link
                            key={index}
                            className={`rounded-full p-2 px-5 ${page === link.href
                                ? "bg-[#2f302d] text-white"
                                : "hover:bg-[#2f302d] hover:text-white"
                                }`}
                            href={link.href}
                        >
                            {link.title}
                        </Link>
                    ))}
                </nav>
            )}
        </>
    );
};

export default function Header() {

    const navigation = [

    ]

    return (
        <>
            <header className="bg-white shadow w-full sticky top-0 z-10 ">
                <nav className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
                    <div className="flex h-16 justify-between">
                        <div className="flex">
                            <div className="flex flex-shrink-0 items-center">
                                <span className="text-xl font-bold text-gray-800">Candle Maker</span>
                            </div>

                            <div className="hidden md:flex">
                                <Navigation links={[{ title: 'Categories', href: '/categories' },
                                { title: 'Components', href: '/components' },
                                { title: 'Candles', href: '/candles' }]} mobile={false} />
                            </div>
                        </div>
                    </div>
                </nav>
            </header>

            <div className="md:hidden flex w-full">
                <Navigation links={[{ title: 'Categories', href: '/categories', icon: <Tag size={28} /> },
                { title: 'Components', href: '/components', icon: <Tag size={28} /> },
                { title: 'Candles', href: '/candles', icon: <Tag size={28} /> }]} mobile={true} />
            </div>
        </>
    )
}
